import React, { useState, useEffect } from 'react';
import { 
  Box, Typography, Container, IconButton, Paper, Grid, ButtonGroup,
  Dialog, DialogTitle, DialogContent, DialogActions, Button, TextField, 
  FormControl, InputLabel, Select, MenuItem, Tooltip
} from '@mui/material';
import WindowControls from '../components/WindowControls';
import { useNavigate } from 'react-router-dom';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import AddIcon from '@mui/icons-material/Add';
import { styled } from '@mui/material/styles';

// Stile per l'animazione dello scorrimento del calendario
const WeekContainer = styled(Box)(({ theme, direction }) => ({
  display: 'flex',
  width: '100%',
  position: 'relative',
  overflow: 'hidden',
  transition: 'all 0.4s ease-in-out',
  transform: direction === 'left' ? 'translateX(-100%)' : 
             direction === 'right' ? 'translateX(100%)' : 
             'translateX(0)',
  opacity: direction === 'none' ? 1 : 0,
}));

// Stile per le celle dei giorni
const DayCell = styled(Paper)(({ theme, isToday }) => ({
  padding: theme.spacing(1),
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  backgroundColor: isToday ? 'rgba(25, 118, 210, 0.1)' : 'rgba(255, 255, 255, 0.05)',
  borderRadius: theme.shape.borderRadius,
  border: isToday ? '1px solid rgba(25, 118, 210, 0.5)' : '1px solid rgba(255, 255, 255, 0.1)',
  '&:hover': {
    backgroundColor: 'rgba(255, 255, 255, 0.1)'
  },
  overflow: 'hidden'
}));

// Stile per l'header dei giorni
const DayHeader = styled(Box)(({ theme }) => ({
  padding: theme.spacing(1),
  textAlign: 'center',
  fontWeight: 'bold',
  borderBottom: '1px solid rgba(255, 255, 255, 0.1)',
  marginBottom: theme.spacing(1)
}));

// Stile per la fascia oraria
const TimeSlot = styled(Box)(({ theme }) => ({
  borderTop: '1px solid rgba(255, 255, 255, 0.05)',
  padding: '2px 0',
  position: 'relative',
  minHeight: '25px', // Altezza minima per slot di 30 minuti
  '&:hover': {
    backgroundColor: 'rgba(255, 255, 255, 0.05)'
  }
}));

// Stile per l'appuntamento
const AppointmentBox = styled(Box)(({ theme, color, duration }) => ({
  position: 'absolute',
  left: '2px',
  right: '2px',
  backgroundColor: color || 'rgba(25, 118, 210, 0.6)',
  borderRadius: theme.shape.borderRadius,
  padding: theme.spacing(0.5),
  overflow: 'hidden',
  cursor: 'pointer',
  zIndex: 1,
  boxShadow: '0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24)',
  transition: 'transform 0.2s ease, box-shadow 0.2s ease',
  '&:hover': {
    transform: 'translateY(-1px)',
    boxShadow: '0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23)'
  }
}));

// Nomi dei giorni della settimana
const DAYS_OF_WEEK = ['Luned', 'Marted', 'Mercoled', 'Gioved', 'Venerd', 'Sabato', 'Domenica'];

// Orari di apertura
const BUSINESS_HOURS = {
  start: 9, // 9:00 AM
  end: 19, // 7:00 PM
  interval: 30 // minuti
};

// Dati di esempio degli operatori
const STYLISTS = [
  { id: 1, name: 'Eddy', color: '#FF5722' },
  { id: 2, name: 'Maria', color: '#9C27B0' },
  { id: 3, name: 'Giovanna', color: '#4CAF50' },
  { id: 4, name: 'Luca', color: '#2196F3' }
];

// Dati di esempio per gli appuntamenti
const MOCK_APPOINTMENTS = [
  { 
    id: 1, 
    title: 'Taglio e piega', 
    client: 'Sara Rossi',
    stylistId: 1, 
    date: new Date(), 
    startTime: new Date().setHours(10, 30), 
    endTime: new Date().setHours(11, 30),
    notes: 'Colore biondo'
  },
  { 
    id: 2, 
    title: 'Colore', 
    client: 'Marco Bianchi',
    stylistId: 2, 
    date: new Date(), 
    startTime: new Date().setHours(14, 0), 
    endTime: new Date().setHours(15, 30),
    notes: 'Tintura rossa'
  },
  { 
    id: 3, 
    title: 'Taglio uomo', 
    client: 'Luca Verdi',
    stylistId: 3, 
    date: new Date(new Date().setDate(new Date().getDate() + 1)), 
    startTime: new Date(new Date().setDate(new Date().getDate() + 1)).setHours(11, 0), 
    endTime: new Date(new Date().setDate(new Date().getDate() + 1)).setHours(11, 30),
    notes: ''
  }
];

function Cliente() {
  const navigate = useNavigate();
  const [currentWeek, setCurrentWeek] = useState(0); // 0  la settimana corrente
  const [animationDirection, setAnimationDirection] = useState('none');
  const [appointments, setAppointments] = useState(MOCK_APPOINTMENTS);
  const [openDialog, setOpenDialog] = useState(false);
  const [selectedDate, setSelectedDate] = useState(null);
  const [selectedTime, setSelectedTime] = useState(null);
  const [newAppointment, setNewAppointment] = useState({
    title: '',
    client: '',
    stylistId: '',
    notes: '',
    duration: 60 // minuti di default
  });
  
  // Ottiene l'inizio della settimana corrente (Luned)
  const getWeekStart = (weekOffset = 0) => {
    const now = new Date();
    const dayOfWeek = now.getDay(); // 0 = Domenica, 1 = Luned, ...
    const diff = now.getDate() - dayOfWeek + (dayOfWeek === 0 ? -6 : 1) + (weekOffset * 7);
    const startDate = new Date(now);
    startDate.setDate(diff);
    return startDate;
  };

  // Calcola le date per la settimana corrente
  const getWeekDates = (weekOffset) => {
    const weekStart = getWeekStart(weekOffset);
    return Array(7).fill(null).map((_, i) => {
      const date = new Date(weekStart);
      date.setDate(weekStart.getDate() + i);
      return date;
    });
  };
  
  // Formatta il range della settimana (es. "10 - 16 Maggio 2023")
  const getWeekRangeText = (weekDates) => {
    const start = weekDates[0];
    const end = weekDates[6];
    
    const startDay = start.getDate();
    const endDay = end.getDate();
    const startMonth = start.toLocaleString('it', { month: 'long' });
    const endMonth = end.toLocaleString('it', { month: 'long' });
    const year = start.getFullYear();
    
    if (startMonth === endMonth) {
      return `${startDay} - ${endDay} ${startMonth} ${year}`;
    }
    return `${startDay} ${startMonth} - ${endDay} ${endMonth} ${year}`;
  };

  // Scorrimento alla settimana precedente
  const previousWeek = () => {
    setAnimationDirection('right');
    setTimeout(() => {
      setCurrentWeek(prev => prev - 1);
      setAnimationDirection('none');
    }, 400);
  };

  // Scorrimento alla settimana successiva
  const nextWeek = () => {
    setAnimationDirection('left');
    setTimeout(() => {
      setCurrentWeek(prev => prev + 1);
      setAnimationDirection('none');
    }, 400);
  };

  // Verifica se una data  oggi
  const isToday = (date) => {
    const today = new Date();
    return date.getDate() === today.getDate() &&
           date.getMonth() === today.getMonth() &&
           date.getFullYear() === today.getFullYear();
  };

  // Genera array di fasce orarie
  const generateTimeSlots = () => {
    const slots = [];
    
    for (let hour = BUSINESS_HOURS.start; hour < BUSINESS_HOURS.end; hour++) {
      for (let minute = 0; minute < 60; minute += BUSINESS_HOURS.interval) {
        slots.push({
          hour,
          minute,
          label: `${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}`
        });
      }
    }
    
    return slots;
  };

  // Formatta orario per la visualizzazione
  const formatTime = (date) => {
    if (!date) return '';
    const hours = new Date(date).getHours().toString().padStart(2, '0');
    const minutes = new Date(date).getMinutes().toString().padStart(2, '0');
    return `${hours}:${minutes}`;
  };

  // Calcola la posizione e l'altezza di un appuntamento
  const getAppointmentPosition = (appointment, date) => {
    // Verifica se l'appuntamento  per questa data
    const appointmentDate = new Date(appointment.date);
    if (appointmentDate.getDate() !== date.getDate() || 
        appointmentDate.getMonth() !== date.getMonth() || 
        appointmentDate.getFullYear() !== date.getFullYear()) {
      return null;
    }
    
    const startTime = new Date(appointment.startTime);
    const endTime = new Date(appointment.endTime);
    
    const startHour = startTime.getHours();
    const startMinute = startTime.getMinutes();
    const endHour = endTime.getHours();
    const endMinute = endTime.getMinutes();
    
    // Calcola la posizione in base all'orario di inizio
    const startPosition = ((startHour - BUSINESS_HOURS.start) * 60 + startMinute) / BUSINESS_HOURS.interval * 25; // 25px per slot
    
    // Calcola l'altezza in base alla durata
    const durationInMinutes = (endHour - startHour) * 60 + (endMinute - startMinute);
    const height = durationInMinutes / BUSINESS_HOURS.interval * 25;
    
    return { top: startPosition, height };
  };

  // Trova il colore dell'operatore
  const getStylistColor = (stylistId) => {
    const stylist = STYLISTS.find(s => s.id === stylistId);
    return stylist ? stylist.color : '#757575'; // Colore grigio di default
  };
  
  // Trova il nome dell'operatore
  const getStylistName = (stylistId) => {
    const stylist = STYLISTS.find(s => s.id === stylistId);
    return stylist ? stylist.name : 'Non assegnato';
  };
  
  // Gestisce apertura del dialog per nuovo appuntamento
  const handleOpenDialog = (date, time) => {
    setSelectedDate(date);
    setSelectedTime(time);
    setOpenDialog(true);
  };
  
  // Chiude il dialog
  const handleCloseDialog = () => {
    setOpenDialog(false);
    setSelectedDate(null);
    setSelectedTime(null);
    setNewAppointment({
      title: '',
      client: '',
      stylistId: '',
      notes: '',
      duration: 60
    });
  };
  
  // Gestisce la creazione di un nuovo appuntamento
  const handleCreateAppointment = () => {
    if (!selectedDate || !selectedTime || !newAppointment.title || !newAppointment.stylistId) {
      return; // Validazione base
    }
    
    const date = new Date(selectedDate);
    const [hour, minute] = selectedTime.split(':').map(Number);
    
    const startTime = new Date(date);
    startTime.setHours(hour, minute);
    
    const endTime = new Date(startTime);
    endTime.setMinutes(endTime.getMinutes() + newAppointment.duration);
    
    const appointment = {
      id: Date.now(), // ID temporaneo
      ...newAppointment,
      date: date,
      startTime: startTime.getTime(),
      endTime: endTime.getTime()
    };
    
    setAppointments(prev => [...prev, appointment]);
    handleCloseDialog();
  };

  const timeSlots = generateTimeSlots();
  const weekDates = getWeekDates(currentWeek);
  const weekRangeText = getWeekRangeText(weekDates);

  return (
    <Container maxWidth="xl" sx={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
      <WindowControls />
      <Box sx={{ 
        p: 3, 
        flexGrow: 1, 
        display: 'flex', 
        flexDirection: 'column',
        position: 'relative'
      }}>
        <IconButton
          onClick={() => navigate(-1)}
          sx={{
            position: 'absolute',
            top: 25,
            left: 0,
            zIndex: 3,
            color: 'white',
            backgroundColor: 'rgba(255, 255, 255, 0.1)',
            '&:hover': {
              backgroundColor: 'rgba(255, 255, 255, 0.2)'
            }
          }}
        >
          <ArrowBackIcon />
        </IconButton>

        <Typography variant="h4" sx={{ 
          mb: 4, 
          color: 'primary.main',
          pl: 6
        }}>
          Calendario Appuntamenti
        </Typography>
        
        {/* Intestazione del calendario con navigazione */}
        <Box sx={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          alignItems: 'center',
          mb: 2
        }}>
          <Typography variant="h6" sx={{ color: 'text.primary' }}>
            Settimana: {weekRangeText}
          </Typography>
          
          <ButtonGroup variant="contained" color="primary">
            <IconButton onClick={previousWeek} color="inherit">
              <ChevronLeftIcon />
            </IconButton>
            <IconButton onClick={() => {
              setCurrentWeek(0);
              setAnimationDirection('none');
            }} color="inherit">
              Oggi
            </IconButton>
            <IconButton onClick={nextWeek} color="inherit">
              <ChevronRightIcon />
            </IconButton>
          </ButtonGroup>
        </Box>
        
        {/* Calendario settimanale */}
        <WeekContainer direction={animationDirection} sx={{ flexGrow: 1, overflow: 'auto' }}>
          <Grid container spacing={1} sx={{ height: 'auto', minHeight: '100%' }}>
            {weekDates.map((date, index) => (
              <Grid item xs key={index} sx={{ height: '100%' }}>
                <DayCell elevation={1} isToday={isToday(date)}>
                  <DayHeader>
                    <Typography variant="subtitle2">
                      {DAYS_OF_WEEK[index]}
                    </Typography>
                    <Typography variant="h6">
                      {date.getDate()}
                    </Typography>
                  </DayHeader>
                  
                  {/* Fasce orarie e appuntamenti */}
                  <Box sx={{ position: 'relative', flexGrow: 1, overflow: 'auto' }}>
                    {timeSlots.map((slot, i) => (
                      <TimeSlot key={i} onClick={() => handleOpenDialog(date, slot.label)}>
                        <Typography variant="caption" sx={{ fontSize: '0.7rem', color: 'text.secondary', userSelect: 'none' }}>
                          {slot.minute === 0 && slot.label}
                        </Typography>
                      </TimeSlot>
                    ))}

                    {/* Visualizzazione degli appuntamenti */}
                    {appointments.map(appointment => {
                      const position = getAppointmentPosition(appointment, date);
                      if (!position) return null;
                      
                      return (
                        <Tooltip 
                          key={appointment.id}
                          title={
                            <>
                              <Typography variant="subtitle2">{appointment.title}</Typography>
                              <Typography variant="body2">Cliente: {appointment.client}</Typography>
                              <Typography variant="body2">
                                Orario: {formatTime(appointment.startTime)} - {formatTime(appointment.endTime)}
                              </Typography>
                              <Typography variant="body2">Operatore: {getStylistName(appointment.stylistId)}</Typography>
                              {appointment.notes && <Typography variant="body2">Note: {appointment.notes}</Typography>}
                            </>
                          }
                        >
                          <AppointmentBox
                            color={getStylistColor(appointment.stylistId)}
                            style={{
                              top: `${position.top}px`,
                              height: `${position.height}px`
                            }}
                          >
                            <Typography variant="caption" sx={{ fontWeight: 'bold', fontSize: '0.7rem' }}>
                              {formatTime(appointment.startTime)} - {appointment.title}
                            </Typography>
                            <Typography variant="caption" sx={{ display: 'block', fontSize: '0.7rem' }}>
                              {appointment.client}
                            </Typography>
                          </AppointmentBox>
                        </Tooltip>
                      );
                    })}
                  </Box>

                  {/* Pulsante per aggiungere appuntamento */}
                  <Box sx={{ 
                    display: 'flex', 
                    justifyContent: 'center', 
                    mt: 1,
                    borderTop: '1px solid rgba(255, 255, 255, 0.1)',
                    pt: 1
                  }}>
                    <IconButton 
                      size="small" 
                      color="primary" 
                      onClick={() => handleOpenDialog(date, '09:00')}
                    >
                      <AddIcon fontSize="small" />
                    </IconButton>
                  </Box>
                </DayCell>
              </Grid>
            ))}
          </Grid>
        </WeekContainer>
      </Box>

      {/* Dialog per aggiungere un appuntamento */}
      <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="sm" fullWidth>
        <DialogTitle>Nuovo Appuntamento</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }}>
            <TextField
              label="Titolo Appuntamento"
              value={newAppointment.title}
              onChange={(e) => setNewAppointment({...newAppointment, title: e.target.value})}
              fullWidth
              required
            />
            
            <TextField
              label="Nome Cliente"
              value={newAppointment.client}
              onChange={(e) => setNewAppointment({...newAppointment, client: e.target.value})}
              fullWidth
              required
            />
            
            <Grid container spacing={2}>
              <Grid item xs={6}>
                {selectedDate && (
                  <TextField
                    label="Data"
                    value={selectedDate.toLocaleDateString('it')}
                    InputProps={{ readOnly: true }}
                    fullWidth
                  />
                )}
              </Grid>
              <Grid item xs={6}>
                <TextField
                  label="Orario Inizio"
                  value={selectedTime || ''}
                  onChange={(e) => setSelectedTime(e.target.value)}
                  type="time"
                  InputLabelProps={{ shrink: true }}
                  fullWidth
                />
              </Grid>
            </Grid>
            
            <FormControl fullWidth required>
              <InputLabel>Durata (minuti)</InputLabel>
              <Select
                value={newAppointment.duration}
                onChange={(e) => setNewAppointment({...newAppointment, duration: e.target.value})}
                label="Durata (minuti)"
              >
                <MenuItem value={30}>30 minuti</MenuItem>
                <MenuItem value={60}>1 ora</MenuItem>
                <MenuItem value={90}>1 ora e 30 min</MenuItem>
                <MenuItem value={120}>2 ore</MenuItem>
                <MenuItem value={150}>2 ore e 30 min</MenuItem>
                <MenuItem value={180}>3 ore</MenuItem>
              </Select>
            </FormControl>

            <FormControl fullWidth required>
              <InputLabel>Operatore</InputLabel>
              <Select
                value={newAppointment.stylistId}
                onChange={(e) => setNewAppointment({...newAppointment, stylistId: e.target.value})}
                label="Operatore"
              >
                {STYLISTS.map(stylist => (
                  <MenuItem key={stylist.id} value={stylist.id}>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <Box 
                        sx={{ 
                          width: 16, 
                          height: 16, 
                          borderRadius: '50%', 
                          bgcolor: stylist.color,
                          mr: 1 
                        }} 
                      />
                      {stylist.name}
                    </Box>
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            
            <TextField
              label="Note"
              value={newAppointment.notes}
              onChange={(e) => setNewAppointment({...newAppointment, notes: e.target.value})}
              multiline
              rows={2}
              fullWidth
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog} color="inherit">Annulla</Button>
          <Button onClick={handleCreateAppointment} variant="contained" color="primary">
            Salva
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}

export default Cliente; 