import React, { createContext, useState, useContext, useRef, useEffect } from 'react';
import RadioBrowserAPI from '../utils/radioAPI';
import RadioStorage from '../utils/radioStorage';

const RadioContext = createContext();

export const RadioProvider = ({ children }) => {
  const [currentRadio, setCurrentRadio] = useState(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [error, setError] = useState(null);
  const [favoritesCount, setFavoritesCount] = useState(0);
  const [historyCount, setHistoryCount] = useState(0);
  const [apiInitialized, setApiInitialized] = useState(false);
  const audioRef = useRef(new Audio());

  // Carica i contatori all'avvio e quando cambiano i dati
  useEffect(() => {
    try {
      updateCounts();
    } catch (err) {
      console.error('Errore nel caricamento dei contatori:', err);
    }
    
    // Inizializza l'API in modo ritardato
    const timer = setTimeout(() => {
      try {
        RadioBrowserAPI.init()
          .then(() => {
            console.log('API Radio inizializzata con successo');
            setApiInitialized(true);
          })
          .catch(err => {
            console.error('Errore nell\'inizializzazione dell\'API Radio:', err);
            setError('Impossibile connettersi ai server radio. Controlla la connessione internet.');
          });
      } catch (err) {
        console.error('Errore critico nell\'inizializzazione dell\'API:', err);
      }
    }, 2000); // Ritardo di 2 secondi
    
    return () => clearTimeout(timer);
  }, []);

  const updateCounts = () => {
    try {
      setFavoritesCount(RadioStorage.getFavorites().length || 0);
      setHistoryCount(RadioStorage.getHistory().length || 0);
    } catch (err) {
      console.error('Errore nell\'aggiornamento dei contatori:', err);
    }
  };

  useEffect(() => {
    if (!currentRadio || !isPlaying) {
      audioRef.current.pause();
      return;
    }
    
    try {
      audioRef.current.src = currentRadio.stream;
      audioRef.current.play()
        .then(() => {
          setError(null);
          
          // Traccia il clic sulla radio solo se l'API  gi inizializzata
          if (currentRadio.id && apiInitialized) {
            RadioBrowserAPI.trackClick(currentRadio.id)
              .catch(err => console.warn('Errore nel tracciamento del click:', err));
          }
          
          // Aggiunge la radio alla cronologia e aggiorna i contatori
          try {
            RadioStorage.addToHistory(currentRadio);
            updateCounts();
          } catch (err) {
            console.warn('Errore nella gestione della cronologia:', err);
          }
        })
        .catch(error => {
          console.error('Errore nella riproduzione:', error);
          setError(`Impossibile riprodurre ${currentRadio.name}. Prova un'altra radio.`);
          setIsPlaying(false);
        });
    } catch (err) {
      console.error('Errore critico nella gestione dell\'audio:', err);
      setError('Si  verificato un errore durante la riproduzione');
      setIsPlaying(false);
    }

    return () => {
      // Non fermiamo l'audio quando il componente viene smontato
    };
  }, [currentRadio, isPlaying, apiInitialized]);

  const playRadio = (radio) => {
    if (currentRadio?.id === radio.id) {
      setIsPlaying(!isPlaying);
    } else {
      setCurrentRadio(radio);
      setIsPlaying(true);
    }
  };

  const stopRadio = () => {
    setIsPlaying(false);
  };

  const togglePlay = () => {
    setIsPlaying(!isPlaying);
  };

  const toggleFavorite = (radio) => {
    try {
      const isFavorite = RadioStorage.isFavorite(radio.id);
      if (isFavorite) {
        RadioStorage.removeFavorite(radio.id);
      } else {
        RadioStorage.addFavorite(radio);
      }
      updateCounts();
      return !isFavorite;
    } catch (error) {
      console.error('Errore nella gestione dei preferiti:', error);
      return false;
    }
  };

  return (
    <RadioContext.Provider value={{
      currentRadio,
      isPlaying,
      error,
      favoritesCount,
      historyCount,
      playRadio,
      stopRadio,
      togglePlay,
      toggleFavorite,
      updateCounts
    }}>
      {children}
    </RadioContext.Provider>
  );
};

export const useRadio = () => {
  const context = useContext(RadioContext);
  if (!context) {
    throw new Error('useRadio deve essere usato all\'interno di un RadioProvider');
  }
  return context;
}; 